* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  font-size: 24px;
}

.country-flag {
  width: 30px;
}

.wrapper-name-country {
  display: flex;
  align-items: center;
}
.wrapper-name-country .country-flag {
  height: 36px;
  width: 36px;
  margin-right: 10px;
}

.title {
  font-weight: 700;
}

.title span {
  font-weight: 400;
}
